import { NavLink } from "react-router-dom";
import { useState } from "react";
import { Squash as Hamburger } from 'hamburger-react'
import HomeIcon from '@mui/icons-material/Home';
import SportsTennisIcon from '@mui/icons-material/SportsTennis';
import StorefrontIcon from '@mui/icons-material/Storefront';
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';
import LoginIcon from "../icons/desktop/LoginIcon";
import AccountIconMobile from "../icons/mobile/AccountIconMobile";
import { useSelector } from "react-redux";



export default function NavbarHamburger() {


  const isConnected = useSelector((state) => state.isConnected);
  
  const [isMenuHamburgerOpen, setToogleMenuHamburger] = useState(false)

  function toogleMenuHamburger() { 
    setToogleMenuHamburger(!isMenuHamburgerOpen);
  }
  
  return (

    <nav  className="navHamburger__contenair">

      <Hamburger  toggled={isMenuHamburgerOpen} toggle={setToogleMenuHamburger}/>

      
      { /*menu dropdown hamburger  */}

      <ul className={`menuDropdownHamburger ${isMenuHamburgerOpen ? " showMenuDropdownHamburger" : ""}`}>

        <li className='menuHamburger__li' onClick={() =>toogleMenuHamburger() } >
            <NavLink  to="/" className={(nav) => (nav.isActive ? "navLink__active" : "navLink__inactive")} end>
              <HomeIcon className="menuHamburger__icon"/>
              Accueil
            </NavLink>
        </li> 

        <li className='menuHamburger__li' onClick={() => toogleMenuHamburger()} >
            <NavLink  to="/cordez" className={(nav) => (nav.isActive ? "navLink__active" : "navLink__inactive")} end>
              <SportsTennisIcon className="menuHamburger__icon"/>
              Cordez
            </NavLink>
        </li>

        <li className='menuHamburger__li' onClick={() => toogleMenuHamburger()} >
            <NavLink  to="/boutique" className={(nav) => (nav.isActive ? "navLink__active" : "navLink__inactive")} end>
              <StorefrontIcon className="menuHamburger__icon"/>
              boutique
            </NavLink>
        </li>

        <li className='menuHamburger__li'  >
           {isConnected? 

             <AccountIconMobile toogleMenuHamburger={toogleMenuHamburger} /> 
            :
             <LoginIcon   toogleMenuHamburger={toogleMenuHamburger}/> 
                               
          }
        </li>
 

        <li className='menuHamburger__li' onClick={() => toogleMenuHamburger()} >
            <NavLink  to="/panier" className={(nav) => (nav.isActive ? "navLink__active" : "navLink__inactive")} end>
              <ShoppingBasketIcon className="menuHamburger__icon"/>
              panier
            </NavLink>
        </li>
      </ul>

    </nav>

  )}