

import { createStore } from "redux";

// On importe la fonction produce d'Immer
import produce from "immer";


// state
const initialState = {
  isConnected: false,
  userData:"",
  dataReset: ""
};



// actions creators

export const connectedToogle = () => ({ type: "connectedToogle" });
export const setUserData = (data) => ({
  type:"setUserData",
  payload:{data: data},
})
export const setDataPasswordReset= (data) => ({
  type:"setDataPasswordReset",
  payload:{data: data},
});

export const changeEmail= (data) => ({
  type:"changeEmail",
  payload:{data: data},
});



function reducer(state = initialState, action) {

  if (action.type === "connectedToogle") {
    return produce(state, (draft)=> {
      draft.isConnected = !draft.isConnected;
    })
  }

  if (action.type === "setUserData") {
    const data = action.payload.data
    return produce(state, (draft)=> {
      draft.userData = data;
    })
  }
  
  if (action.type === "setDataPasswordReset") {
    const data = action.payload.data
    return produce(state, (draft)=> {
      draft.dataReset = data ;
    })
  }

  if (action.type === "changeEmail") {
    const newEmail = action.payload.data
    return produce(state, (draft)=> {
      draft.userData.email = newEmail ;
    })
  }


  return state;
}

export const store = createStore(reducer);



