import { useState } from 'react';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { Link } from "react-router-dom"
import { useForm} from "react-hook-form"
import { yupResolver } from '@hookform/resolvers/yup'
import { shemaInputStringing } from "../../Utils/shemaInput"


export default function Stringing() {

  const clubOptions = [
      { value: 'TC Pont' },
      { value: 'TC Penmach'},
      { value: 'TC Quimperlé' },
    ];

  const [shopOptions,setShopOptions] = useState([{value : " "}])
  console.log(shopOptions)

  // gestion du contôle de la validité des inputs 
  const { register , formState, handleSubmit,  } =
  useForm({
            resolver: yupResolver(shemaInputStringing),
            mode: 'onTouched',
            shouldFocusError: true,
          });
  const {isSubmitting, isValid} = formState


  //fonction asynchrone vers le backend récupérant la liste
  // des magazins associé à un club (en paramètre) dans la bdd
  const loadShopOptions = async function (data) {
    try{
      const response = await fetch(`http://localhost:3001/api/shop/shopChoice`, {
        mode: "cors",
        method: "POST",
        body: JSON.stringify({club: data }),
        headers: {"Content-Type": "application/json"}})

        if (!response.ok) {
          const result = await response.json();
          throw new Error(` ${result.message}`);
        }

        else {
        const result = await response.json();
        console.log(result.message)
        const listShop = result.stringers
        const shopOptions = listShop.map(( x ) => ({ value: x.nameShop}))
        console.log(shopOptions)
        setShopOptions(shopOptions)
      }
    }

    catch(err){
      const errorMessage = err.toString();
      console.log(errorMessage);
    }

  }

  //fonction asynchrone vers le backend récupérant la liste
  // des marques associé à un shop (en paramètre) dans la bdd
  const loadBrandStringOptions = async function (data) {
    try{
      const response = await fetch(`http://localhost:3001/api/shop/shopChoice`, {
        mode: "cors",
        method: "POST",
        body: JSON.stringify({club: data }),
        headers: {"Content-Type": "application/json"}})

        if (!response.ok) {
          const result = await response.json();
          throw new Error(` ${result.message}`);
        }

        else {
        const result = await response.json();
        console.log(result.message)
        const listShop = result.stringers
        const shopOptions = listShop.map(( x ) => ({ value: x.nameShop}))
        console.log(shopOptions)
        setShopOptions(shopOptions)
      }
    }

    catch(err){
      const errorMessage = err.toString();
      console.log(errorMessage);
    }

  }

  //fonction asynchrone vers le backend récupérant la liste
  // des cordages associé à une marque (en paramètre) dans la bdd
  const loadStringOptions = async function (data) {
    try{
      const response = await fetch(`http://localhost:3001/api/shop/shopChoice`, {
        mode: "cors",
        method: "POST",
        body: JSON.stringify({club: data }),
        headers: {"Content-Type": "application/json"}})

        if (!response.ok) {
          const result = await response.json();
          throw new Error(` ${result.message}`);
        }

        else {
        const result = await response.json();
        console.log(result.message)
        const listShop = result.stringers
        const shopOptions = listShop.map(( x ) => ({ value: x.nameShop}))
        console.log(shopOptions)
        setShopOptions(shopOptions)
      }
    }

    catch(err){
      const errorMessage = err.toString();
      console.log(errorMessage);
    }

  }


  //fonction asynchrone vers le backend enregistrant la pré-commande
  const onSubmit = async function (data) {
    try{
      const response = await fetch(`http://localhost:3001/api/user/signup`, {
        mode: "cors",
        method: "POST",
        body: JSON.stringify({civilite: data.civilite, lastname: data.lastname, forename: data.forename, email: data.email, password: data.password}),
        headers: {"Content-Type": "application/json"}})

        if (!response.ok) {
          const result = await response.json();

          throw new Error(` ${result.message}`);
        }else {


        const result = await response.json();

        console.log(result.userData)
        console.log(result.message)
        console.log(result.token)
      }
    }

    catch(err){
      const errorMessage = err.toString();
      console.log(errorMessage);
    }
  }


  return (


    <main className="main-stringing">

      <div className="bg-stinging"> </div>


      <section className="stringing-contenair">

        <div className="breadcrumb-wrapper">
          <div className="contenair-fluid">
            <ul className="breadcrump breadcrump-stringing">
              <li>
                <Link to='/'className="link__breadcrump"> Accueil </Link>
              </li>
              <li>
                /  cordez
              </li>
            </ul>
          </div>
        </div>

        <div className="form__contenair">

          <div className="form__header form__header-stringing">

            <h1 className="form__header__h1">
             Commandez votre cordage!
            </h1>

            <span className="littleBitText">
              Remplissez les différents champs ci-dessous, cliquez et commandez .
            </span>

          </div>

          <div className="form__field-wrapper">


            <form onSubmit={handleSubmit(onSubmit)} className="form__stringing">

              {/* selection du club */}
              <label className="select__label" >Club </label>
              <div className='select__wrapper'>
                <select 
                className="select__bar" 
                {...register("club")}
                onChange={e => { loadShopOptions(e.target.value);}}
                >
                  {clubOptions.map(( option) =>
                  (<option value = {option.value} > {option.value} </option>))}
                  </select>
                  <KeyboardArrowDownIcon className='select__arrow' />
              </div>

              {/* selection du shop */}
              <label className="select__label" >Cordeur préféré  </label>
              <div className='select__wrapper'>
                <select 
                className="select__bar" 
                {...register("shop")}
                onChange={e => { loadBrandStringOptions(e.target.value);}}
                >
                {shopOptions.map(( x ) =>
                  (<option value = {x.value} > {x.value} </option>))}
                  </select>
                <KeyboardArrowDownIcon className='select__arrow' />
              </div>

              {/* selection de la marque du cordage */}
              <label className="select__label" >Marque de cordage  </label>
              <div className='select__wrapper'>
                <select 
                className="select__bar" 
                {...register("shop")}
                onChange={e => { loadStringOptions(e.target.value);}}
                >
                  {shopOptions.map(( x ) =>
                  (<option value = {x.value} > {x.value} </option>))}
                </select>
                <KeyboardArrowDownIcon className='select__arrow' />
              </div>

              {/* selection du cordage */}
              <label className="select__label" >Cordage  </label>
              <div className='select__wrapper'>
                <select 
                className="select__bar" 
                {...register("string")}
                >
                {shopOptions.map(( x ) =>
                (<option value = {x.value} > {x.value} </option>))}
                </select>
                <KeyboardArrowDownIcon className='select__arrow' />
              </div>

              {/* selection de la tension */}
              <label className="select__label" >Tension de cordage </label>
              <div className='select__wrapper'>
                <select 
                className="select__bar" 
                {...register("rope-tension")}
                >
                {shopOptions.map(( x ) =>
                (<option value = {x.value} > {x.value} </option>))}
                </select>
                <KeyboardArrowDownIcon className='select__arrow' />
              </div>

              <div className="buttons-wrapper-stringing">
                <button disabled={isSubmitting || !isValid} type="submit" className={"button__submit "}>
                   Push cordage in the popoche
                    {!isValid? <div className="button__submit-bg-invalid"></div>: 
                    <div className="button__submit-bg"></div>}
                </button>
              </div>

            </form>

          </div>

        </div>

      </section>

    </main>

    )
  }

  