import { Link } from "react-router-dom"
import LogoutIconMobile from "../icons/mobile/LogoutIconMobile"
import BackNavSubmenu from "../button/BackNavSubmenu";
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';


export default function AccountSubmenuMobile(props) {


  const toogleMobileSubmenu = props.toogleMobileSubmenu
  const toogleMenuHamburger = props.toogleMenuHamburger

  function toogleMenuHamburgerAndSubmenu(){
    toogleMenuHamburger();
    toogleMobileSubmenu();
  }


  return(

    <div className='submenuMobile__contenair'>
        
        <BackNavSubmenu   toogleMobileSubmenu ={toogleMobileSubmenu} />

  
      <div className='submenuMobile__wrapper-title'>
           Mon compte
      </div>

      <div className='submenuMobile__wrapper-links'>

        <ul className='submenuMobile__ul'>
            <li className='submenuMobile__li' onClick={()=> toogleMenuHamburgerAndSubmenu()}  >
              <Link  to="/compte" className="submenuMobile__link">
              Profil 
              </Link>
              <ArrowForwardIosIcon className="submenuMobile__icon-forward"/>
            </li> 

            <li className='submenuMobile__li'  onClick={()=> toogleMenuHamburgerAndSubmenu()}  >
              <Link  to="/historique_commandes" className="submenuMobile__link">
              Commandes
              </Link>
              <ArrowForwardIosIcon className="submenuMobile__icon-forward"/>
            </li> 

        </ul>
      </div>

      <div className='submenuMobile__logout'>
          <LogoutIconMobile  toogleMenuHamburgerAndSubmenu={toogleMenuHamburgerAndSubmenu}/>
      </div>

    </div>

  )
}